/* Section/Article */

section.special,
article.special {
  text-align: center;
}

header p {
  color: #999;
  margin: 0 0 1.5em 0;
  position: relative;
}

header h2 + p {
  font-size: 1.25em;
  line-height: 1.5em;
  margin-top: -1em;
}

header h3 + p {
  font-size: 1.1em;
  line-height: 1.5em;
  margin-top: -0.8em;
}

header h4 + p,
header h5 + p,
header h6 + p {
  font-size: 0.9em;
  line-height: 1.5em;
  margin-top: -0.6em;
}

header.major {
  margin: 0 0 1em 0;
}

header.major:after {
  background: rgba(144, 144, 144, 0.5);
  content: "";
  display: inline-block;
  height: 1px;
  margin-top: 1.5em;
  width: 6em;
}

header.major h2,
header.major h3,
header.major h4,
header.major h5,
header.major h6 {
  margin: 0;
}

header.major p {
  margin: 0.7em 0 0 0;
}

header.special {
  text-align: center;
}

/* Form */

form {
  margin: 0 0 2em 0;
}

label {
  color: #555;
  display: block;
  font-size: 0.9em;
  font-weight: 400;
  margin: 0 0 1em 0;
}

input[type="text"],
input[type="password"],
input[type="email"],
select,
textarea {
  -moz-appearance: none;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  /* background: rgba(144, 144, 144, 0.075); */
  border-radius: 4px;
  border: none;
  color: rgb(210, 214, 213);
  border: solid 1px rgba(144, 144, 144, 0.5);
  /* color: inherit; */
  display: block;
  outline: 0;
  padding: 0 1em;
  text-decoration: none;
  width: 100%;
}

input[type="text"]:invalid,
input[type="password"]:invalid,
input[type="email"]:invalid,
select:invalid,
textarea:invalid {
